@use "@/styles/mixins";

.sidebar {
  display: none;
  position: fixed;
  padding: 54px;
  width: 320px;
  color: var(--white);

  @include mixins.lg {
    display: block;
  }

  @include mixins.xl {
    width: 380px;
  }

  ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  button {
    display: flex;

    &:hover {
      & > div:last-child {
        text-decoration-color: currentColor;
        text-underline-offset: 6px;
      }
    }

    & > div:first-child {
      width: 28px;
      flex-shrink: 0;
    }

    & > div:last-child {
      transition: all 150ms ease-in;
      text-underline-offset: 4px;
      text-decoration: underline;
      text-decoration-thickness: 2px;
      text-decoration-color: transparent;
    }
  }
}

.main {
  grid-template-columns: 12px 1fr;

  @include mixins.lg {
    grid-template-columns: 320px 12px 1fr 12px;
  }

  @include mixins.xl {
    grid-template-columns: 380px 54px 1fr 54px;
  }
}

.content {
  grid-area: content;
  position: relative;
  margin-bottom: 100px;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  background: linear-gradient(
    180deg,
    rgba(247, 247, 247, 0.8) 0%,
    rgba(247, 247, 247, 0.5) 10.69%
  );

  @include mixins.lg {
    margin-bottom: 160px;
    padding: 52px;
  }

  &__header {
    display: flex;
    flex-direction: column;
    gap: 24px;
    border-bottom: 1px solid var(--white-40);
    padding: 24px;

    @include mixins.lg {
      flex-direction: row;
      padding: 0 0 30px;
    }

    & > * {
      flex: 1 1 0px;
    }
  }

  &__main {
    display: flex;
    flex-direction: column;
    gap: 54px;
    margin: 24px 0;
    padding: 0 24px 24px;

    @include mixins.lg {
      padding: 52px;
    }
  }

  &__alinea {
    display: flex;
    flex-direction: column;
    gap: 24px;

    ul {
      list-style: none;
      display: flex;
      flex-direction: column;
      gap: 12px;
    }

    li {
      display: flex;
      gap: 12px;

      &::before {
        content: "";
        flex-shrink: 0;
        background-color: var(--black);
        display: inline-block;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        margin-top: 10px;
      }
    }
  }

  &__links {
    display: flex;
    flex-direction: column;
    gap: 4px;

    a {
      text-decoration: underline;
      cursor: pointer;
    }
  }
}
